body {
  margin: 0 auto;
  max-width: 1260px;
}
body a.arrowDown {
  font-size: 30px;
  text-decoration: none;
  color: #fc635e;
  border: 1px solid #fc635e;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  border-radius: 50%;
  width: 45px;
  height: 45px;
}
body a.arrowDown:hover {
  background: #fc635e;
  color: white;
}
body a.btn {
  color: #fc635e;
  text-decoration: none;
  font-size: 22px;
  border: 2px solid #db5854;
  padding: 12px 28px;
  border-radius: 8px;
}
body a.btn:hover {
  background: #db5854;
  color: #fff;
}
body .mainText {
  text-align: center;
}
body .mainText h1 {
  text-transform: uppercase;
  font-size: 50px;
  color: white;
}
body .mainText h1 span {
  color: #fc635e;
}
body .mainText p {
  color: white;
  font-size: 22px;
}
body h1.main_header {
  margin: 0;
  text-transform: uppercase;
  font-size: 40px;
}
body h1.main_header span {
  color: #fc635e;
}
body header {
  background: url('../img/background.png') no-repeat;
  display: flex;
  flex-direction: column;
  min-height: 600px;
  width: 100%;
  max-width: 1260px;
  justify-content: space-around;
}
body header nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
body header nav > a {
  color: white;
  font-size: 25px;
  font-weight: bold;
  text-decoration: none;
}
body header nav > a img {
  padding-right: 15px;
  vertical-align: middle;
}
body header nav .logo:hover img {
  opacity: 0.7;
}
body header nav menu {
  list-style-type: none;
  text-transform: uppercase;
  display: flex;
}
body header nav menu li {
  padding-left: 15px;
}
body header nav menu li a {
  color: white;
  font-size: 20px;
  text-decoration: none;
}
body header nav menu li a:hover {
  color: #fc635e;
  border-bottom: 3px solid #fc635e;
  padding-bottom: 5px;
}
body .recive {
  margin-top: 0;
  text-align: center;
  background: white;
  padding: 70px 50px 10px 50px;
  color: #868686;
}
body .recive p {
  font-size: 22px;
  margin-bottom: 50px;
}
body .icons {
  display: flex;
}
body .icons h3 {
  text-decoration: underline;
  text-transform: uppercase;
}
body .icons p {
  font-size: 18px;
}
body .icons div {
  padding: 15px;
}
body .expend {
  padding: 100px 100px 15px 100px;
  background: url(../img/expand.png) no-repeat;
}
body .expend .mainText {
  padding: 50px;
  margin-bottom: 80px;
  border: 15px solid rgba(0, 0, 0, 0.2);
}
body .expend .mainText h1 {
  font-size: 55px;
  margin-bottom: 50px;
}
body .our_process {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 15px;
}
body .our_process .steps {
  display: none;
}
body .our_process h1,
body .our_process p {
  color: gray;
}
body .our_process h1 {
  text-decoration: underline;
}
body .our_process p {
  font-size: 18px;
}
body .our_process .back_process {
  min-height: 500px;
  min-width: auto;
  background-image: url(../img/Our_process.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

@media screen and (max-width: 768px) and (min-width: 550px) {
  nav {
    flex-direction: column;
    justify-content: center;
  }
  body .expend {
    padding: 30px 30px 10px 30px;
  }
  body .expend .mainText {
    padding: 50px 0px;
    margin-bottom: 20px;
  }
  body .expend .mainText h1 {
    font-size: 30px;
    margin-bottom: 50px;
  }
}
@media screen and (max-width: 549px) {
  nav {
    padding: 0px 20px;
  }
  menu {
    display: flex;
    flex-direction: column;
  }
  menu li {
    margin-bottom: 10px;
  }
  body header .mainText h1 {
    margin-top: -20px;
    font-size: 40px;
  }
  body header .mainText p {
    font-size: 18px;
    padding: 20px 10px;
  }
  body .expend {
    padding: 10px;
  }
  body .expend .mainText {
    border: none;
    padding: 30px 0px;
    margin-bottom: 20px;
  }
  body .expend .mainText h1 {
    font-size: 25px;
    margin-bottom: 30px;
  }
}
@media screen and (max-width: 1260px) {
  .recive {
    padding: 30px 20px;
  }
  .recive h1 {
    font-size: 26px;
  }
  .recive p {
    font-size: 18px;
    margin-bottom: 20px;
  }
  .icons {
    flex-wrap: wrap;
    justify-content: center;
  }
  .icons div {
    max-width: 350px;
  }
  .icons div p {
    margin-bottom: 20px;
  }
}
